import { Component } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-main',
  templateUrl: './app.html',
  standalone: false,
  styleUrl: './app.scss'
})
export class App {
  constructor(private router: Router) {}

  //        
  navigateTo(path: string): void {
    this.router.navigate([path]);
  }

  //  
  goHome(): void {
    this.router.navigate(['/']);
  }
}