import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { BehaviorSubject, Observable } from 'rxjs';
import { RecentFile, FileItem, FileManagerConfig, ViewMode } from '../shared/interfaces/common.interfaces';

@Injectable({
  providedIn: 'root'
})
export class FileManagerService {
  private readonly RECENT_FILES_KEY = 'ezReaderRecentFiles';
  private recentFilesSubject = new BehaviorSubject<RecentFile[]>([]);
  public recentFiles$ = this.recentFilesSubject.asObservable();

  private config: FileManagerConfig = {
    maxRecentFiles: 5, //    
    supportedImageFormats: ['image/jpeg', 'image/png', 'image/gif', 'image/webp'],
    supportedDocumentFormats: ['application/pdf', '.doc', '.docx', '.txt'],
    maxFileSize: 50 * 1024 * 1024 // 50MB
  };

  constructor(private router: Router) {
    this.loadRecentFiles();
  }

  //   
  loadRecentFiles(): RecentFile[] {
    const data = localStorage.getItem(this.RECENT_FILES_KEY);
    const files = data ? JSON.parse(data) : [];
    this.recentFilesSubject.next(files);
    return files;
  }

  saveRecentFiles(files: RecentFile[]): void {
    try {
      // localStorage    JSON   
      const jsonString = JSON.stringify(files);
      const size = new Blob([jsonString]).size;
      
      // 5MB     
      if (size > 5 * 1024 * 1024) {
        const reducedFiles = files.slice(0, Math.floor(files.length / 2));
        localStorage.setItem(this.RECENT_FILES_KEY, JSON.stringify(reducedFiles));
        this.recentFilesSubject.next(reducedFiles);
        return;
      }
      
      localStorage.setItem(this.RECENT_FILES_KEY, jsonString);
      this.recentFilesSubject.next(files);
    } catch (error) {
      
      // QuotaExceededError 
      if (error instanceof DOMException && error.name === 'QuotaExceededError') {
        // localStorage     
        this.clearExcessiveData();
        
        //   (PDF  )
        const metadataOnly = files.map(file => ({
          name: file.name,
          date: file.date,
          size: file.size,
          data: '' // PDF  
        })).slice(0, 3); //  3
        
        try {
          localStorage.setItem(this.RECENT_FILES_KEY, JSON.stringify(metadataOnly));
          this.recentFilesSubject.next(metadataOnly);
        } catch (retryError) {
          //     
          localStorage.removeItem(this.RECENT_FILES_KEY);
          this.recentFilesSubject.next([]);
        }
      }
    }
  }

  // localStorage  
  public clearExcessiveData(): void {
    try {
      // ezReader    
      const keysToCheck = ['currentPdf', 'currentPdfName', 'pdfData'];
      keysToCheck.forEach(key => {
        const item = localStorage.getItem(key);
        if (item && item.length > 1024 * 1024) { // 1MB 
          localStorage.removeItem(key);
        }
      });
      
      //   PDF  
      const recentFiles = this.loadRecentFiles();
      const cleanedFiles = recentFiles.map(file => ({
        ...file,
        data: file.data && file.data.length > 10000 ? '' : file.data //   
      }));
      
      if (cleanedFiles.length !== recentFiles.length || 
          cleanedFiles.some((file, index) => file.data !== recentFiles[index].data)) {
        localStorage.setItem(this.RECENT_FILES_KEY, JSON.stringify(cleanedFiles));
      }
    } catch (error) {
    }
  }

  addRecentFile(name: string, data: string, size: string): void {
    const now = new Date().toISOString();
    const currentFiles = this.recentFilesSubject.value;
    
    // PDF      ( )
    const shouldStoreData = data.length < 100 * 1024; // 100KB  
    const fileData = shouldStoreData ? data : '';
    
    if (!shouldStoreData) {
    }
    
    const updatedFiles = [
      { name, date: now, data: fileData, size },
      ...currentFiles.filter(f => f.name !== name)
    ].slice(0, this.config.maxRecentFiles);
    
    this.saveRecentFiles(updatedFiles);
  }

  deleteRecentFile(fileToDelete: RecentFile): void {
    const currentFiles = this.recentFilesSubject.value;
    const updatedFiles = currentFiles.filter(f => f.name !== fileToDelete.name);
    this.saveRecentFiles(updatedFiles);
  }

  //   
  handleFileUpload(acceptTypes: string[], multiple: boolean = false): Promise<FileList | null> {
    return new Promise((resolve) => {
      const input = document.createElement('input');
      input.type = 'file';
      input.multiple = multiple;
      input.accept = acceptTypes.join(',');
      
      input.onchange = (e: any) => {
        resolve(e.target.files);
      };
      
      input.onclick = () => {
        //   null 
        setTimeout(() => {
          if (!input.files) {
            resolve(null);
          }
        }, 1000);
      };
      
      input.click();
    });
  }

  // PDF  
  async processPdfFile(file: File): Promise<string> {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.onload = () => {
        const base64 = reader.result as string;
        this.addRecentFile(file.name, base64, this.formatFileSize(file.size));
        resolve(base64);
      };
      reader.onerror = () => reject(reader.error);
      reader.readAsDataURL(file);
    });
  }

  //  
  openInViewer(pdfData: string, fileName: string, viewMode: ViewMode = ViewMode.DESKTOP): void {
    const route = viewMode === ViewMode.MOBILE ? '/docs_mobile/viewer' : '/viewer';
    this.router.navigate([route], { 
      state: { pdfData, name: fileName } 
    });
  }

  //   
  formatFileSize(bytes: number): string {
    if (bytes === 0) return '0 Bytes';
    const k = 1024;
    const sizes = ['Bytes', 'KB', 'MB', 'GB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
  }

  //    
  handleFileDrop(event: DragEvent): File | null {
    event.preventDefault();
    const files = event.dataTransfer?.files;
    
    if (files && files.length > 0) {
      const file = files[0];
      const isPdf = file.type === 'application/pdf' || file.name.toLowerCase().endsWith('.pdf');
      
      if (isPdf) {
        return file;
      } else {
        //   
        const event = new CustomEvent('show-snackbar', { 
          detail: { message: 'PDF    .' }
        });
        document.dispatchEvent(event);
        return null;
      }
    }
    return null;
  }

  //   
  async handleFileAction(action: string, files: FileItem[]): Promise<void> {
    switch (action) {
      case 'open':
        if (files.length > 0 && files[0].data) {
          this.openInViewer(files[0].data, files[0].name);
        }
        break;
      case 'share':
        break;
      case 'rename':
        break;
      case 'copy':
        break;
      case 'delete':
        break;
      default:
    }
  }

  // localStorage   
  getStorageInfo(): { used: number; total: number; percentage: number } {
    let used = 0;
    for (let key in localStorage) {
      if (localStorage.hasOwnProperty(key)) {
        used += localStorage.getItem(key)?.length || 0;
      }
    }
    
    //  localStorage  5-10MB
    const total = 5 * 1024 * 1024; // 5MB 
    const percentage = (used / total) * 100;
    
    return { used, total, percentage };
  }

  clearAllRecentFiles(): void {
    localStorage.removeItem(this.RECENT_FILES_KEY);
    this.recentFilesSubject.next([]);
  }

  //  localStorage  
  emergencyCleanup(): void {
    const storageInfo = this.getStorageInfo();
    
    // ezReader   
    const keysToRemove = Object.keys(localStorage).filter(key => 
      key.startsWith('ezReader') || 
      key.includes('pdf') || 
      key.includes('Pdf') ||
      key === 'currentPdf' ||
      key === 'currentPdfName'
    );
    
    keysToRemove.forEach(key => {
      localStorage.removeItem(key);
    });
    
    this.recentFilesSubject.next([]);
  }
}
