import { platformBrowser } from '@angular/platform-browser';
import { AppModule } from './app/app-module';
import { initializeConsoleOverride, toggleConsoleOverride, getConsoleOverrideStatus } from './app/utils/console-override.util';
import { initializeAlertOverride } from './app/utils/alert-override.util';
import { initializeConfirmOverride } from './app/utils/confirm-override.util';

// Node.js polyfills for browser (docx )
import { Buffer } from 'buffer';
import process from 'process';

(window as any).Buffer = Buffer;
(window as any).process = process;
(window as any).global = window;

// Console   (   )
//        
initializeConsoleOverride();

// Alert   ( alert Snackbar )
//        
initializeAlertOverride();

// Confirm   ( confirm Dialog )
//        
initializeConfirmOverride();

//    (   )
(window as any).toggleConsoleOverride = toggleConsoleOverride;
(window as any).getConsoleOverrideStatus = getConsoleOverrideStatus;

platformBrowser().bootstrapModule(AppModule, {
  ngZoneEventCoalescing: true,
})
  .catch(err => console.warn('[Bootstrap Error]', err));
