import { NgModule, provideBrowserGlobalErrorListeners } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { FormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';
import { JwtModule } from '@auth0/angular-jwt';

import { AppRoutingModule } from './app-routing-module';
import { AppRootComponent } from './app-root.component';
import { App } from './app';

export function tokenGetter() {
  //  loworld  
  const loworldToken = localStorage.getItem('loworld_accessToken');
  if (loworldToken) {
    return loworldToken;
  }
  
  //     
  const storeMode = localStorage.getItem('ez_store_auth_mode') || 'CUSTOMER';
  switch (storeMode) {
    case 'CUSTOMER':
      return localStorage.getItem('ez_store_customer_token');
    case 'SELLER':
      return localStorage.getItem('ez_store_seller_token');
    case 'ADMIN':
      return localStorage.getItem('ez_store_admin_token');
    default:
      return null;
  }
}

//  imports
import { AuthService } from './services/auth.service';
import { GeminiTranslationService } from './services/gemini-translation.service';
import { LocalAiTranslationService } from './services/local-ai-translation.service';
import { FileManagerService } from './services/file-manager.service';
import { FileConversionService } from './services/file-conversion.service';
import { NovelGeneratorService } from './modules/novel/services/novel-generator.service';

// Modules
import { SharedModule } from './modules/shared/shared.module';

@NgModule({
  declarations: [
    AppRootComponent,
    App
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    FormsModule,
    HttpClientModule,
    JwtModule.forRoot({
      config: {
        tokenGetter: tokenGetter,
        allowedDomains: ['localhost:3000', 'api.loworldsoft.com'],
        disallowedRoutes: []
      }
    }),
    SharedModule,
    AppRoutingModule
  ],
  providers: [
    provideBrowserGlobalErrorListeners(),
    GeminiTranslationService,
    LocalAiTranslationService,
    FileManagerService,
    FileConversionService,
    NovelGeneratorService,
    AuthService
  ],
  bootstrap: [AppRootComponent]
})
export class AppModule { }
