import { Injectable } from '@angular/core';
import { ConversionSettings, ConversionOption } from '../shared/interfaces/common.interfaces';

@Injectable({
  providedIn: 'root'
})
export class FileConversionService {
  
  //   
  getConversionOptions(): ConversionOption[] {
    return [
      {
        id: 'image',
        title: '  PDF',
        icon: './images/ico-img@3x.png',
        acceptTypes: ['image/*']
      },
      {
        id: 'excel',
        title: '  PDF',
        icon: './images/btn-gnb-xls@3x.png',
        acceptTypes: ['.xlsx', '.xls']
      },
      {
        id: 'powerpoint',
        title: '  PDF',
        icon: './images/btn-gnb-ppt@3x.png',
        acceptTypes: ['.pptx', '.ppt']
      },
      {
        id: 'word',
        title: '  PDF',
        icon: './images/btn-gnb-doc@3x.png',
        acceptTypes: ['.docx', '.doc']
      }
    ];
  }

  //   
  getUploadOptions(): ConversionOption[] {
    return [
      {
        id: 'file',
        title: '',
        icon: '',
        acceptTypes: ['.pdf', '.doc', '.docx', '.txt']
      },
      {
        id: 'photo',
        title: '',
        icon: '',
        acceptTypes: ['image/*']
      },
      {
        id: 'pdf',
        title: 'PDF',
        icon: '',
        acceptTypes: ['.pdf']
      }
    ];
  }

  //   
  async convertFiles(files: FileList, conversionType: string, settings: ConversionSettings): Promise<boolean> {
    try {
      
      //     
      //  
      await this.simulateConversion(files.length);
      
      return true;
    } catch (error) {
      return false;
    }
  }

  //  
  private async simulateConversion(fileCount: number): Promise<void> {
    return new Promise(resolve => {
      setTimeout(resolve, 1000 + (fileCount * 500)); //     
    });
  }

  //    
  getDefaultSettings(): ConversionSettings {
    return {
      fileName: '_',
      savePath: '/Users/Downloads',
      quality: 80,
      format: 'pdf'
    };
  }

  //   
  validateFileType(file: File, acceptTypes: string[]): boolean {
    const fileName = file.name.toLowerCase();
    const fileType = file.type;

    return acceptTypes.some(acceptType => {
      if (acceptType.startsWith('.')) {
        return fileName.endsWith(acceptType);
      } else if (acceptType.includes('/*')) {
        const baseType = acceptType.split('/')[0];
        return fileType.startsWith(baseType);
      } else {
        return fileType === acceptType;
      }
    });
  }

  //    
  getSupportedFormatsMessage(acceptTypes: string[]): string {
    return ` : ${acceptTypes.join(', ')}`;
  }
}
