import { Component, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { environment } from '../environments/environment';
import { DebugLoggerService } from './shared/services/debug-logger.service';
import { SnackbarService } from './services/snackbar.service';
import { setDebugLoggerInstance } from './utils/console-override.util';
import { setSnackbarServiceInstance, setMatDialogInstance } from './utils/alert-override.util';
import { setConfirmDialogInstance } from './utils/confirm-override.util';

@Component({
  selector: 'app-root',
  template: `
    <router-outlet></router-outlet>
    @if (showGlobalNavigation) {
      <app-global-navigator></app-global-navigator>
    }
  `,
  standalone: false
})
export class AppRootComponent implements OnInit {
  title = 'ezReader';
  
  constructor(
    private titleService: Title,
    private debugLogger: DebugLoggerService,
    private snackbarService: SnackbarService,
    private matDialog: MatDialog,
    private router: Router
  ) {
    // Console override debugLogger  
    setDebugLoggerInstance(this.debugLogger);
    
    // Alert override snackbarService  
    setSnackbarServiceInstance(this.snackbarService);
    
    // Alert override matDialog  
    setMatDialogInstance(this.matDialog);
    
    // Confirm override matDialog  
    setConfirmDialogInstance(this.matDialog);
  }
  
  ngOnInit(): void {
    //     
    this.titleService.setTitle('Loworldsoft');
  }
  
  get showGlobalNavigation(): boolean {
    // note, calendar, desk   
    if (this.router.url.startsWith('/note') || this.router.url.startsWith('/calendar') || this.router.url.startsWith('/desk')) {
      return false;
    }
    return environment.showGlobalNavigation === true;
  }
}
