import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { GeminiTranslationService } from '../../../services/gemini-translation.service';

export interface NovelSettings {
  genre: string;
  protagonist: string;
  setting: string;
  theme: string;
  length: string;
  style: string;
}

export interface NovelGenerationResult {
  success: boolean;
  novel?: string;
  error?: string;
}

@Injectable({
  providedIn: 'root'
})
export class NovelGeneratorService {

  constructor(
    private http: HttpClient,
    private geminiService: GeminiTranslationService
  ) {}

  async generateNovel(settings: NovelSettings): Promise<NovelGenerationResult> {
    try {

      //    
      const prompt = this.buildNovelPrompt(settings);
      
      // Gemini AI   
      const result = await this.geminiService.translateText(prompt, 'korean').toPromise();
      
      if (result && result.translatedText) {
        return {
          success: true,
          novel: result.translatedText
        };
      } else {
        return {
          success: false,
          error: '  .'
        };
      }

    } catch (error) {
      return {
        success: false,
        error: error instanceof Error ? error.message : '    .'
      };
    }
  }

  private buildNovelPrompt(settings: NovelSettings): string {
    const { genre, protagonist, setting, theme, length, style } = settings;

    //  
    const genreDescriptions = {
      'traditional': '   ',
      'modern': '   ',
      'fantasy': '    ',
      'historical': '   '
    };

    //  
    const settingDescriptions = {
      'ancient': '  ',
      'joseon': '  ',
      'modern': '   ',
      'fantasy': '  '
    };

    //  
    const themeDescriptions = {
      'revenge': '  ',
      'love': '   ',
      'friendship': '  ',
      'justice': '   ',
      'power': '   ',
      'adventure': '  '
    };

    //  
    const lengthStructures = {
      'short': '3-5      ',
      'medium': '8-12     ',
      'long': '15-20     '
    };

    //  
    const styleTones = {
      'classic': '   ',
      'modern': '   ',
      'humorous': '  ',
      'serious': '   '
    };

    const prompt = `
   .     :

** :**
- : ${protagonist}
- : ${genreDescriptions[genre as keyof typeof genreDescriptions]}
- : ${settingDescriptions[setting as keyof typeof settingDescriptions]}
- : ${themeDescriptions[theme as keyof typeof themeDescriptions]}
- : ${lengthStructures[length as keyof typeof lengthStructures]}
- : ${styleTones[style as keyof typeof styleTones]}

** :**
1.      
2. , ,       
3.        
4.     
5.  ,      

**:**
-  ()
-  (    )
-  ()

  "${protagonist}"     !
`;

    return prompt;
  }

  //   
  generateNovelKeywords(settings: NovelSettings): string[] {
    const keywords = [
      settings.protagonist,
      '', '', '', '', '',
      '', '', '', '', '',
      '', '', '', '', '',
      '', '', '', '', '',
      '', '', '', '', ''
    ];

    //   
    if (settings.genre === 'fantasy') {
      keywords.push('', '', '', '', '');
    }
    if (settings.theme === 'love') {
      keywords.push('', '', '', '', '');
    }

    return keywords;
  }
}