//    

export interface RecentFile {
  name: string;
  date: string;
  size: string;
  data: string;
}

export interface FileItem {
  id: string;
  name: string;
  size: string;
  type: string;
  lastModified: string;
  date?: string; //     
  data?: string;
}

export interface ConversionSettings {
  fileName: string;
  savePath: string;
  quality: number;
  format: string;
}

export interface ConversionOption {
  id: string;
  title: string;
  icon: string;
  acceptTypes: string[];
}

export interface FileAction {
  id: string;
  label: string;
  icon: string;
  dangerous?: boolean;
}

export interface FileManagerConfig {
  maxRecentFiles: number;
  supportedImageFormats: string[];
  supportedDocumentFormats: string[];
  maxFileSize: number;
}

export enum FileType {
  PDF = 'pdf',
  IMAGE = 'image',
  DOCUMENT = 'document',
  FOLDER = 'folder'
}

export enum ViewMode {
  DESKTOP = 'desktop',
  MOBILE = 'mobile'
}
